import React, { Component } from "react";
import classes from "./SightingBox.module.css";

class SightingBox extends Component {
  constructor(props) {
    super(props);

    this.state = {
      sightingId: props.sightingId,
      name: props.name,
      X:props.X,
      Y:props.Y,
      Width:props.Width,
      Height:props.Height,
      Voted: 0
    };

  }

  postVote(vote){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ vote: vote , sightingId :this.state.sightingId })
      };
      fetch('http://tecspec.dedicated.co.za/abcapi/api/vote', requestOptions)
      .then(response => {
        
        console.log(response.json());
        this.setState({
          Voted: 1,
        });
      });
  }

  render(){
    return (
      
    <div className={classes.SightingDiv} style={{ left: this.state.X ,top: this.state.Y ,height: this.state.Height,width: this.state.Width}}>
       {this.state.Voted == 0 && <div  className={classes.VoteButtonDiv} >
        <label className={classes.VoteButtonLabel}>{this.state.name}</label>
        <div>
        <button className={classes.VoteButton} onClick={() => this.postVote(2)}>Yes</button>
        <button className={classes.VoteButton} onClick={() => this.postVote(1)}>No</button>
        <button className={classes.VoteButton} onClick={() => this.postVote(0)}>Unsure</button>
        </div>
      
      </div>}
      {this.state.Voted == 1 &&
           <div className={classes.VoteButtonResultDiv} >
             <label className={classes.VoteButtonLabel}>Thank you</label>
           </div>
      }
    </div>
    )};
};

export default SightingBox;
